"use client"

import { useState, useEffect } from "react"
import { useParams } from "next/navigation"
import Image from "next/image"
import { Clock, Trophy, FileText, Users, ChevronLeft } from "lucide-react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Badge } from "@/components/ui/badge"
import { toast } from "sonner"

// Mock data - replace with your actual data fetching logic
const fetchCampaign = async (id) => {
  // Simulate API call
  return {
    id,
    name: "Community Garden Project",
    description: "Vote on which plants to include in our neighborhood garden project.",
    image: "/placeholder.svg?height=200&width=400",
    votingType: "proposal", // "proposal" or "candidate"
    resultType: "one-winner", // "one-winner" or "rank-based"
    startTime: new Date(Date.now() + 1000 * 60 * 60 * 2).toISOString(), // 2 hours from now
    endTime: new Date(Date.now() + 1000 * 60 * 60 * 24 * 2).toISOString(), // 2 days from now
    accessRestriction: "open", // "open", "limited", or "registration"
    passkey: "garden2024", // Only used if accessRestriction is "registration"
    maxVoters: 100, // Only used if accessRestriction is "limited"
  }
}

export default function CampaignPage() {
  const params = useParams()
  const campaignId = params.id

  const [campaign, setCampaign] = useState(null)
  const [loading, setLoading] = useState(true)
  const [passkey, setPasskey] = useState("")
  const [registered, setRegistered] = useState(false)
  const [candidateForm, setCandidateForm] = useState({
    name: "",
    reason: "",
    image: null,
  })
  const [timeRemaining, setTimeRemaining] = useState({
    hours: 0,
    minutes: 0,
    seconds: 0,
  })
  const [votingStarted, setVotingStarted] = useState(false)

  useEffect(() => {
    const loadCampaign = async () => {
      try {
        setLoading(true)
        const data = await fetchCampaign(campaignId)
        setCampaign(data)

        // Check if voting has started
        const now = new Date()
        const startTime = new Date(data.startTime)
        setVotingStarted(now >= startTime)
      } catch (error) {
        console.error("Failed to load campaign:", error)
        toast.error("Failed to load campaign details")
      } finally {
        setLoading(false)
      }
    }

    loadCampaign()
  }, [campaignId])

  useEffect(() => {
    if (!campaign || votingStarted) return

    const updateCountdown = () => {
      const now = new Date()
      const startTime = new Date(campaign.startTime)

      if (now >= startTime) {
        setVotingStarted(true)
        return
      }

      const diff = startTime.getTime() - now.getTime()

      const hours = Math.floor(diff / (1000 * 60 * 60))
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60))
      const seconds = Math.floor((diff % (1000 * 60)) / 1000)

      setTimeRemaining({ hours, minutes, seconds })
    }

    updateCountdown()
    const interval = setInterval(updateCountdown, 1000)

    return () => clearInterval(interval)
  }, [campaign, votingStarted])

  const handlePasskeySubmit = (e) => {
    e.preventDefault()

    if (passkey === campaign.passkey) {
      // In a real app, you would mint an NFT here
      setRegistered(true)
      toast.success("Registration successful!", {
        description: "Your voter NFT has been minted.",
      })
    } else {
      toast.error("Invalid passkey", {
        description: "Please check the passkey and try again.",
      })
    }
  }

  const handleCandidateSubmit = (e) => {
    e.preventDefault()

    if (!candidateForm.name || !candidateForm.reason) {
      toast.error("Missing required fields", {
        description: "Please fill in all required fields.",
      })
      return
    }

    // In a real app, you would submit the candidate to the blockchain
    toast.success("Candidate registration successful!", {
      description: "Your candidacy has been registered.",
    })

    // Reset form
    setCandidateForm({
      name: "",
      reason: "",
      image: null,
    })
  }

  const handleImageChange = (e) => {
    const file = e.target.files[0]
    if (file) {
      setCandidateForm({
        ...candidateForm,
        image: file,
      })
    }
  }

  if (loading) {
    return (
      <div className="container max-w-4xl mx-auto py-8">
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto"></div>
            <p className="mt-4 text-muted-foreground">Loading campaign details...</p>
          </div>
        </div>
      </div>
    )
  }

  if (!campaign) {
    return (
      <div className="container max-w-4xl mx-auto py-8">
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-4">Campaign Not Found</h2>
          <p className="text-muted-foreground mb-6">
            The campaign you're looking for doesn't exist or couldn't be loaded.
          </p>
          <Button asChild>
            <Link href="/">Back to Home</Link>
          </Button>
        </div>
      </div>
    )
  }

  // If voting has started, redirect to voting interface
  if (votingStarted) {
    return (
      <div className="container max-w-4xl mx-auto py-8">
        <div className="mb-6">
          <Link href="/" className="flex items-center text-muted-foreground hover:text-foreground transition-colors">
            <ChevronLeft className="h-4 w-4 mr-1" />
            Back to campaigns
          </Link>
        </div>

        <Card className="mb-6">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl">Voting is now open!</CardTitle>
            <CardDescription>You are being redirected to the voting interface.</CardDescription>
          </CardHeader>
          <CardContent className="flex justify-center">
            <Button asChild size="lg">
              <Link href={`/campaign/${campaignId}/vote`}>Go to Voting Interface</Link>
            </Button>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="container max-w-4xl mx-auto py-8">
      <div className="mb-6">
        <Link href="/" className="flex items-center text-muted-foreground hover:text-foreground transition-colors">
          <ChevronLeft className="h-4 w-4 mr-1" />
          Back to campaigns
        </Link>
      </div>

      <Card className="mb-6">
        <CardHeader>
          <div className="flex justify-between items-start">
            <div>
              <CardTitle className="text-2xl">{campaign.name}</CardTitle>
              <CardDescription className="mt-2">{campaign.description}</CardDescription>
            </div>
            <Badge variant="outline" className="ml-2">
              Upcoming
            </Badge>
          </div>
        </CardHeader>

        {campaign.image && (
          <CardContent className="p-0">
            <div className="relative w-full h-48">
              <Image
                src={campaign.image || "/placeholder.svg"}
                alt={campaign.name}
                fill
                className="object-cover rounded-md"
              />
            </div>
          </CardContent>
        )}

        <CardContent className="pt-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <div className="flex items-center gap-2">
                <FileText className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="text-sm text-muted-foreground">Voting Type</p>
                  <p className="font-medium">
                    {campaign.votingType === "proposal" ? (
                      <span className="flex items-center gap-1">
                        Proposal-based <FileText className="h-4 w-4" />
                      </span>
                    ) : (
                      <span className="flex items-center gap-1">
                        Candidate-based <Trophy className="h-4 w-4" />
                      </span>
                    )}
                  </p>
                </div>
              </div>

              <div className="flex items-center gap-2">
                <Trophy className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="text-sm text-muted-foreground">Result Type</p>
                  <p className="font-medium">
                    {campaign.resultType === "one-winner" ? (
                      <span className="flex items-center gap-1">
                        One winner <Badge className="ml-1">ðŸ¥‡</Badge>
                      </span>
                    ) : (
                      <span className="flex items-center gap-1">
                        Rank-based <Badge className="ml-1">ðŸ”¢</Badge>
                      </span>
                    )}
                  </p>
                </div>
              </div>
            </div>

            <div className="space-y-4">
              <div className="flex items-center gap-2">
                <Clock className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="text-sm text-muted-foreground">Voting Period</p>
                  <p className="font-medium">
                    {new Date(campaign.startTime).toLocaleDateString()} -{" "}
                    {new Date(campaign.endTime).toLocaleDateString()}
                  </p>
                </div>
              </div>

              <div className="flex items-center gap-2">
                <Users className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="text-sm text-muted-foreground">Access Restriction</p>
                  <p className="font-medium">
                    {campaign.accessRestriction === "open"
                      ? "Open to all"
                      : campaign.accessRestriction === "limited"
                        ? `Limited Voters (Max: ${campaign.maxVoters})`
                        : "Requires Registration"}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Countdown Timer */}
        <Card>
          <CardHeader>
            <CardTitle>Voting Countdown</CardTitle>
            <CardDescription>Time remaining until voting begins</CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <div className="text-4xl font-bold mb-2">
              {String(timeRemaining.hours).padStart(2, "0")}:{String(timeRemaining.minutes).padStart(2, "0")}:
              {String(timeRemaining.seconds).padStart(2, "0")}
            </div>
            <p className="text-muted-foreground">
              Voting starts in: {timeRemaining.hours}h {timeRemaining.minutes}m {timeRemaining.seconds}s
            </p>
          </CardContent>
        </Card>

        {/* Registration or Candidate Form */}
        {campaign.accessRestriction === "registration" && !registered ? (
          <Card>
            <CardHeader>
              <CardTitle>Voter Registration</CardTitle>
              <CardDescription>Enter the passkey to register as a voter</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handlePasskeySubmit}>
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Input
                      type="password"
                      placeholder="Enter passkey"
                      value={passkey}
                      onChange={(e) => setPasskey(e.target.value)}
                    />
                  </div>
                  <Button type="submit" className="w-full">
                    Register to Vote
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        ) : campaign.accessRestriction === "registration" && registered ? (
          <Card>
            <CardHeader>
              <CardTitle>Registration Complete</CardTitle>
              <CardDescription>You are registered to vote in this campaign</CardDescription>
            </CardHeader>
            <CardContent className="text-center">
              <div className="mb-4">
                <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-green-100 dark:bg-green-900 mb-4">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-8 w-8 text-green-600 dark:text-green-300"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                </div>
                <p>Your voter NFT has been minted successfully.</p>
              </div>
              <p className="text-sm text-muted-foreground">You will be able to vote once the voting period begins.</p>
            </CardContent>
          </Card>
        ) : null}

        {/* Candidate Registration Form */}
        {campaign.votingType === "candidate" && (
          <Card className={campaign.accessRestriction !== "registration" || registered ? "" : "md:col-span-2"}>
            <CardHeader>
              <CardTitle>Register as a Candidate</CardTitle>
              <CardDescription>Fill out the form to become a candidate in this campaign</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleCandidateSubmit}>
                <div className="space-y-4">
                  <div className="space-y-2">
                    <label htmlFor="name" className="text-sm font-medium">
                      Candidate Name <span className="text-red-500">*</span>
                    </label>
                    <Input
                      id="name"
                      placeholder="Your name"
                      value={candidateForm.name}
                      onChange={(e) => setCandidateForm({ ...candidateForm, name: e.target.value })}
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <label htmlFor="reason" className="text-sm font-medium">
                      Why should people vote for you? <span className="text-red-500">*</span>
                    </label>
                    <Textarea
                      id="reason"
                      placeholder="Explain why you're the best candidate"
                      value={candidateForm.reason}
                      onChange={(e) => setCandidateForm({ ...candidateForm, reason: e.target.value })}
                      required
                      className="min-h-[100px]"
                    />
                  </div>

                  <div className="space-y-2">
                    <label htmlFor="image" className="text-sm font-medium">
                      Profile Image (Optional)
                    </label>
                    <Input id="image" type="file" accept="image/*" onChange={handleImageChange} />
                  </div>

                  <Button type="submit" className="w-full">
                    Register as Candidate
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}

